        width: 0
    }
    
    .slider {
        background-color: #ccc;
        border-radius: 8.5px;
        bottom: 0;
        cursor: pointer;
        left: 0;
        right: 0;
        top: 0
    }
    
    .slider,
    .slider:before {
        position: absolute;
        transition: .4s
    }
    
    .slider:before {
        background-color: #fff;
        border-radius: 50%;
        bottom: 1.5px;
        content: "";
        height: 14px;
        left: 4px;
        width: 14px
    }
    
    input:checked+.slider {
        background-color: #2196f3
    }
    
    input:focus+.slider {
        box-shadow: 0 0 1px #2196f3
    }
    
    input:checked+.slider:before {
        transform: translateX(8px)
    }
    
    .slider.round {
        border-radius: 8.5px
    }
    
    .slider.round:before {
        border-radius: 50%
    }
    
